export default {
  data() {
    return {
      ulStyle: { width: "750px", paddingLeft: "20px" }, // 
      itemWidth: 750, // ,
      swiperStyle: {}, // 
      index: 0, // 
      touchStart: {}, // 
      touchEnd: {}, // 
      intervalTime: "", // 
      isTrue: true
    };
  },
  props: {
    height: {
      type: String,
      default: "3rem"
    },
    options: {
      type: Object,
      default: {
        showDots: true, // 
        interval: 3000, // 3s
        autoplay: true, // 
        loop: true // 
      }
    },
    list: {
      type: Array,
      default: []
    },
    effect: {
      type: String,
      default: "zoom" //  normalzoom
    }
  },
  mounted() {
    this.calcWidth();
    this.handleLoopMove();
  },
  methods: {
    /**
     * 
     */
    calcWidth() {
      // 
      this.$nextTick(function() {
        if (this.effect === "normal") {
          // 
          this.itemWidth = document.body.clientWidth; // 
        } else if (this.effect === "zoom") {
          // 
          this.itemWidth = document.body.clientWidth - 40; // 
        }
        this.handleType();
        var length = this.list.length; // 
        this.ulStyle.width = parseInt((this.itemWidth + 40) * length) + "px"; // 
      });
    },
    /**
     * 
     */
    handleClick(val) {
      // 
      this.$emit("onClick", val);
    },
    /**
     * 
     */
    handleType() {
      if (this.effect === "normal") {
        this.ulStyle.paddingLeft = 0; // padding0
      } else if (this.effect === "zoom") {
        this.ulStyle.paddingLeft = "20px"; // 
      }
    },
    /**
     * 
     */
    handleMove() {
      let moveX = this.itemWidth * this.index;
      if (this.index === 0) {
        moveX = 0;
        this.handleType();
      } else {
        this.ulStyle.paddingLeft = 0; // padding0
        if (this.effect === "zoom") {
          moveX = moveX - 20;
        }
      }
      this.swiperStyle = {
        transform: "translateX(-" + moveX + "px)"
      };
    },
    /**
     * 
     */
    handleLoopMove() {
      // 
      if (this.options.autoplay) {
        let interval = this.options.interval ? this.options.interval : 3000;
        this.intervalTime = setInterval(() => {
          if (this.isTrue) this.index++;
          else this.index--;
          if (this.index == 0 && this.isTrue == false) {
            this.index = 0;
            this.isTrue = true;
          } else if (this.index > this.list.length - 1) {
            this.index = this.list.length - 2; // -1,index0
            this.isTrue = false;
          // this.index++;
          // if (this.index > this.list.length - 1) {
          //   this.index = 0; // -1,index0
          }
          this.handleMove();
        }, interval);
      }
    },
    /**
     * 
     */
    onTouchStart(e) {
      this.touchStart = e.changedTouches[0]; // 
      // 
      clearInterval(this.intervalTime);
    },
    /**
     * 
     */
    onTouchEnd(e) {
      this.touchEnd = e.changedTouches[0];
      // 
      if (this.touchEnd.clientX - this.touchStart.clientX > 60) {
        this.index--;
        if (this.index <= 0) {
          this.index = 0;
        }
      } else if (this.touchEnd.clientX - this.touchStart.clientX < -60) {
        this.index++;
        if (this.index >= this.list.length - 1) {
          this.index = this.list.length - 1;
        }
      }
      // 
      this.handleMove();
      // 
      this.handleLoopMove();
    }
  },
  watch: {
    list: function(e) {
      this.calcWidth();
    }
  },
  destroyed() {
    // 
    if (this.autoplay) {
      clearInterval(this.intervalTime);
    }
  }
};



